@import url('https://fonts.googleapis.com/css?family=Audiowide');

.body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.page-display {
    width: 100%;
    height: 100%;
    display: inline-block;
}

.body-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100%;
}

.sidebar-nav {
    display: inline-block;
    justify-content: space-evenly;
    align-items: center;
    width: 18vw;
    height: 90vh;
    border-right: thick solid #0984e3;
    padding-right: 1vw;
}

.sidebar-nav button{
    width: 100%;
    margin: auto;
    height: 17.5%;
    font-size: 4vh;
    font-family: 'Audiowide';
    background-color: #353b48;;
    color: white;
    border: 1px solid #0984e3;
    border-radius: 5vh;
    margin-bottom: 1vh;
    margin-top: 1vh;
}


.middle-body-container {
    justify-content: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}


.profile-container {
    display: inline-flex;
    justify-content: center;
    text-align: left;
    width: auto;
    height: auto;
}

.profile-display-item {
    margin: auto;
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: white;
    margin: 1.5vh;
}

.profile-description {
    font-size: 5vh;
    margin-right: 1vw;
    color: rgb(138, 136, 136);
    
}

.profile-value {
    font-size: 5vh;
    color: white;
}

.register-form {
    align-items: flex-start;
    display: block;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    height: 100%;
}

.input-title {
    display: inline-block;
    color: white;
    margin-top: 2.5vh;
    width: auto;
    font-size: 5vh;
}

.input-box {
    height: 5vh;
    width: 10vw;
    background: #0984e3;
    border-radius: 1vh;
}

.phone-container {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    margin: 1.5vh;
    margin-bottom: 0vh;
}

.phone-input {
    width: 50%;
}

.submit-button {
    width: auto;
    height: 100%;
    font-size: 2.5vh;
    font-weight: 100;
    padding: 1vh;
    padding-left: 5vh;
    padding-right: 5vh;
    border-radius: 3vh;
    margin-top: 3vh;
    color: white;
    border: .05vh solid #0984e3;
    background-color: #353b48;
}

.dropdown-options {
    font-size: 5vh;
    height: 3.5vh;
    border-radius: 1.5vh;
    width: 20vw;
}

.alert-div {
    display: block;
    margin: auto;
    width: 100%;
    color: white;
    position: absolute;
    z-index: 1;
}






@keyframes content_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}
