@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.endzone-text-input-div {
    display: inline-block;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 2vh;
    width: auto;
    padding: 1vh;
    padding-bottom: 0;
    padding-top: 0;
}

.endzone-text-input {
    font-size: 2vh;
    max-width: 100%;
    font-family: "Audiowide";
    color: white;
    border: none;
    border-bottom: .1vh solid #0984e3;
    background-color: rgba(0, 0, 0, 0);
    margin-top: 2vh;
}
